@import "variables";
@import "mixin";
.contact-page {
  width: 100vw;
  height: 100vh;
  background-image: url("../img/wall.jpg");
  background-size: cover;
  background-position: bottom;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(4, 1fr);

  .a {
    grid-column-start: 1;
    grid-column-end: 3;

    @include centralize;
    color: #1a1b1b;
    font-weight: lighter;
    font-size: xx-large;
    letter-spacing: 1rem;
    font-family: Arial, Helvetica, sans-serif;
    p {
      background-color: #1d1e20;
      background-image: url("../img/paper-1074131_1920.jpg");
      background-size: contain;
      background-position: bottom;
      width: 100%;
      height: 4rem;

      opacity: 1;
      @include centralize;
    }
    text-shadow: 1rem 0 1rem #000000cb, 0 0 1rem #000000ee;
  }
  .b {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 2;
    grid-row-end: 5;
    form {
      padding: 0 1rem 0 1rem;
      margin: 10% 0 0 5%;
      width: 50%;
      height: 80%;
      border: 2px solid wheat;
      background-image: url("../img/paper-1074131_1920.jpg");
      background-size: contain;
      background-position: bottom;
      border-radius: 10px;

      h5 {
        margin-bottom: 0.5rem;
        color: #1a1b1b;
        font-weight: lighter;
        font-family: Arial, Helvetica, sans-serif;
        letter-spacing: 3px;
      }
      .full-name {
        width: 50%;
        @include contactForm;
      }
      .email {
        width: 50%;
        margin-bottom: 3rem;
        @include contactForm;
      }
      .message {
        padding-top: 0.5rem;
        width: 99%;
        color: black;
        @include contactForm;
        height: 5rem; // this one overwrite the hight the contactForm mixin
      }
      .submit-button {
        margin-top: 2rem;
        width: 100%;
        letter-spacing: 4rem;
        font-weight: bolder;
        @include contactForm;
      }
    }
  }
  .c {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: 5;
    @include centralize;
  }
}
